<script>
  import { onMount } from "svelte";
  import { slide } from "svelte/transition";

  import Gallery from "svelte-brick-gallery";
  import Image from "svelte-brick-gallery/Image.svelte";

  const version = "0.0.1";

  /**
   * @type {string | any[]}
   */
  let images = [];
  let itemHeight = 150;
  let gap = 0;

  let height = 120;
  let pad = 10;
  let justify = "left";

  const scale = (w, h) => {
    return Math.floor((height * w) / h);
  };

  let page = 1;
  const getImages = async () => {
    let limit = 10;
    if (page === 1) {
      limit = 30;
    }
    let res = await fetch(
      `https://picsum.photos/v2/list?page=${page}&limit=${limit}`
    );

    images = images.concat(
      (await res.json()).map((img) => ({
        src: `https://picsum.photos/id/${img.id}/${
          scale(img.width, img.height) + pad
        }/${height + pad}`,
        id: img.id,
        width: img.width,
        height: img.height,
      }))
    );

    if (limit === 30) {
      page = 3;
    }
    page++;

    return images;
  };

  const load = getImages();

  let scroll;
  let winHeight = 0;
  let bodyHeight = 0;

  let viewedSrc = "";
  let viewed = false;

  const setViewed = (id, width, height) => {
    viewedSrc = `https://picsum.photos/id/${id}/${width}/${height}`;
    viewed = true;
  };

  let infiniteLoad = true;
  let roundBorder = false;
  let hoverEffect = false;
  let animated = false;

  $: if (scroll > 0 && winHeight > 0) {
    if (Math.ceil(scroll + winHeight) >= bodyHeight) {
      console.log("bot");
      if (infiniteLoad) {
        getImages();
      }
    }
  }

  let currentTheme = "dark";

  const setTheme = (theme) => {
    document.body.classList.remove(currentTheme);
    currentTheme = theme;
    document.body.classList.add(currentTheme);
  };

  window
    .matchMedia("(prefers-color-scheme: dark)")
    .addEventListener("change", (event) => {
      console.log("bt");
      setTheme(event.matches ? "dark" : "light");
    });

  $: setTheme(currentTheme);

  onMount(() => {
    if (window.matchMedia) {
      if (window.matchMedia("(prefers-color-scheme: light)").matches) {
        currentTheme = "light";
      }
    }
  });

  function animate(node, options) {
    if (animated) {
      return options.animation(node, options);
    }
  }
</script>

<svelte:window bind:innerHeight={winHeight} bind:scrollY={scroll} />

<div class="flex txt-default" bind:clientHeight={bodyHeight}>
  <div class="w-1/3">
    <div class="h-screen w-1/3 fixed flex flex-col bg-color-2">
      <div class="flex-1">
        <div class="p-2">
          <label>
            Gap
            <input type="range" min="0" max="48" bind:value={gap} />
          </label>
        </div>
        <div class="p-2">
          <label>
            itemHeight
            <input
              type="range"
              min="100"
              max="500"
              step="10"
              bind:value={itemHeight}
            />
          </label>
        </div>
        <div>
          <h4>justify</h4>
          <label>
            <input type="radio" bind:group={justify} value="left" />
            left
          </label>
          <label>
            <input type="radio" bind:group={justify} value="center" />
            center
          </label>
          <label>
            <input type="radio" bind:group={justify} value="right" />
            right
          </label>
        </div>
        <div>
          <h2 class="text-lg">things you can easily add yourself</h2>
          <div>
            <label
              >border radius
              <input type="checkbox" bind:checked={roundBorder} />
            </label>
          </div>
          <div>
            <label
              >hover effect
              <input type="checkbox" bind:checked={hoverEffect} />
            </label>
          </div>
          <div>
            <label
              >transition
              <input type="checkbox" bind:checked={animated} />
            </label>
          </div>
          <div>
            <label>
              infinite loading
              <input type="checkbox" bind:checked={infiniteLoad} />
            </label>
          </div>
        </div>
      </div>
      <div>
        <button
          on:click={() => setTheme(currentTheme == "dark" ? "light" : "dark")}
          >theme</button
        >
        <span>demo {version}</span>
      </div>
    </div>
  </div>
  <div class="flex-1 overflow-x-hidden">
    {#await load}
      loading...
    {:then}
      <div class={gap === 0 ? "p-0" : "p-4"}>
        <Gallery {images} {justify} {itemHeight} itemWidth={500} {gap}>
          <div
            slot="image"
            let:index
            let:style
            style="height:100%; position:relative;"
          >
            <img
              transition:animate={{ animation: slide, duration: 325 }}
              src={images[index].src}
              style={style + (roundBorder ? " border-radius: 12px;" : "")}
              class={hoverEffect ? "hover:scale-105" : ""}
              alt={index}
              on:click={() =>
                setViewed(
                  images[index].id,
                  images[index].width,
                  images[index].height
                )}
            />
          </div>
        </Gallery>
      </div>
    {:catch}
      loading error
    {/await}
  </div>
</div>

{#if viewed}
  <div
    class="fixed z-10 top-0 p-10  h-screen w-full bg-slate-500/75"
    on:click={() => (viewed = false)}
  >
    <div class="h-full">
      <Image src={viewedSrc} classes="w-full h-full object-contain" />
    </div>
    <button class="fixed top-0 right-0" on:click={() => (viewed = false)}
      >close</button
    >
  </div>
{/if}

<style lang="postcss" global>
  @tailwind base;
  @tailwind components;
  @tailwind utilities;

  html,
  body {
    margin: 0;
    padding: 0;
  }

  body {
    @apply bg-color-1;
  }

  :root {
    --color-1: black;
    --color-2: black;
    --color-3: black;
    --color-4: black;
    --text-default: black;
    --text-secondary: black;
  }

  .dark {
    --color-1: #1e1e1e;
    --color-2: #272727;
    --color-3: #353535;
    --color-4: #505050;
    --text-default: #848484;
    --text-secondary: #505050;
  }

  .light {
    --color-1: #fdf6e3;
    --color-2: #dbd5c1;
    --color-3: #c9c3b3;
    --color-4: #beb7a5;
    --text-default: #6e6d6b;
    --text-secondary: #a79e83;
  }

  .bg-color-1 {
    background-color: var(--color-1);
  }
  .bg-color-2 {
    background-color: var(--color-2);
  }
  .bg-color-3 {
    background-color: var(--color-3);
  }
  .bg-color-4 {
    background-color: var(--color-4);
  }
  .txt-default {
    color: var(--text-default);
  }
  .txt-secondary {
    color: var(--text-secondary);
  }
</style>
